.close-icon{
  width: 25px;
  border: none;
  background-color: white;
}

.mobile {
  background-color: white;
  height: 100vh;
  width: 60vw;
  position: absolute;
  right: -55px;
  top: -6px;
  padding: 18px 12px;
  display: flex;
  flex-direction: column;
  border: 1px solid orange
}

.close-icon{
  display: flex;
  justify-content: flex-end;
  font-size: 24px;
}

.mobile-options{
  display: flex;
  flex-direction: column;
  width: 100%;
  padding-top: 24px;
  align-items: center;
  height: 100%;
}

.mobile-option {
  font-size: 24px;
  margin: 12px 0px;
  display: flex;
}

.mobile-option a{
  text-decoration: none;
  color: black;
  display: flex;
}
